.summary-panel {
  margin-bottom: 16px;
}

.summary-title {
  font-size: 18px;
  font-weight: 500;
  color: var(--text-primary);
  margin-bottom: 16px;
}

.summary-stats {
  display: flex;
  gap: 16px;
  margin-bottom: 20px;
}

.stat-item {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.stat-label {
  font-size: 12px;
  color: var(--text-secondary);
}

.stat-value {
  font-size: 20px;
  font-weight: 700;
  color: var(--text-primary);
}

.conflict-score {
  margin-bottom: 20px;
}

.score-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.score-label {
  font-size: 14px;
  color: var(--text-secondary);
}

.score-value {
  font-size: 18px;
  font-weight: 700;
  color: var(--text-primary);
}

.score-gauge {
  width: 100%;
  height: 12px;
  background: var(--card-bg);
  border-radius: 6px;
  box-shadow: 
    inset 2px 2px 4px var(--shadow-dark),
    inset -2px -2px 4px var(--shadow-light);
  overflow: hidden;
}

.score-fill {
  height: 100%;
  background: linear-gradient(90deg, var(--accent-positive) 0%, #ff9800 100%);
  border-radius: 6px;
  transition: width 0.3s ease;
}

.emotional-fill {
  background: linear-gradient(90deg, #e3f2fd 0%, #2196f3 50%, #f44336 100%);
}

.communication-fill {
  background: linear-gradient(90deg, #f44336 0%, #ff9800 50%, var(--accent-positive) 100%);
}

.emotional-intensity,
.communication-score {
  margin-bottom: 20px;
}

.fault-ratios-summary {
  margin-bottom: 16px;
}

.fault-title {
  font-size: 14px;
  font-weight: 500;
  color: var(--text-primary);
  margin-bottom: 12px;
}

.fault-chart {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.fault-item {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.fault-bar-wrapper {
  width: 100%;
  height: 8px;
  background: var(--card-bg);
  border-radius: 4px;
  box-shadow: 
    inset 2px 2px 4px var(--shadow-dark),
    inset -2px -2px 4px var(--shadow-light);
  overflow: hidden;
}

.fault-bar {
  height: 100%;
  background: linear-gradient(90deg, #4caf50 0%, #ff9800 50%, #f44336 100%);
  border-radius: 4px;
  transition: width 0.3s ease;
}

.fault-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
}

.fault-speaker {
  color: var(--text-secondary);
}

.fault-percentage {
  font-weight: 600;
  color: var(--text-primary);
}

.selected-settlement {
  margin-top: 16px;
  padding: 12px;
  background: var(--card-bg);
  border-radius: 12px;
  box-shadow: 
    inset 2px 2px 4px var(--shadow-dark),
    inset -2px -2px 4px var(--shadow-light);
}

.settlement-title {
  font-size: 14px;
  font-weight: 500;
  color: var(--text-primary);
  margin-bottom: 8px;
}

.settlement-info {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.settlement-amount {
  font-size: 20px;
  font-weight: 700;
  color: var(--accent-positive);
}

.settlement-confidence {
  font-size: 12px;
  color: var(--text-secondary);
}

@media (max-width: 768px) {
  .summary-stats {
    flex-direction: column;
    gap: 12px;
  }
}

