.header-bar {
  position: sticky;
  top: 0;
  z-index: 100;
  background-color: var(--bg-color);
  padding: 12px 16px;
  border-bottom: 1px solid var(--shadow-dark);
}

.header-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1400px;
  margin: 0 auto;
}

.header-title h1 {
  font-size: 18px;
  font-weight: 500;
  color: var(--text-primary);
  margin: 0;
}

.header-button {
  width: 44px;
  height: 44px;
  color: var(--text-primary);
}

@media (max-width: 768px) {
  .header-title h1 {
    font-size: 16px;
  }
}

