.disclaimer-banner {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 16px 20px;
  background: linear-gradient(135deg, #ffebee 0%, #ffcdd2 100%);
  border-radius: 12px;
  box-shadow: 
    4px 4px 8px var(--shadow-dark),
    -4px -4px 8px var(--shadow-light);
  color: var(--accent-highlight);
  font-size: 13px;
  font-weight: 500;
  line-height: 1.5;
  margin: 16px;
}

.disclaimer-banner svg {
  flex-shrink: 0;
  color: var(--accent-highlight);
}

.disclaimer-text {
  flex: 1;
}

@media (max-width: 768px) {
  .disclaimer-banner {
    padding: 12px 16px;
    font-size: 12px;
    margin: 12px;
  }
}

