.transcript-timeline {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-height: 0;
}

.timeline-title {
  font-size: 18px;
  font-weight: 500;
  color: var(--text-primary);
  margin-bottom: 16px;
}

.timeline-content {
  flex: 1;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 16px;
  padding-right: 8px;
}

.utterance-card {
  position: relative;
  transition: var(--transition);
}

.utterance-card.highlighted {
  box-shadow: 
    8px 8px 16px var(--shadow-dark),
    -8px -8px 16px var(--shadow-light),
    0 0 0 2px var(--accent-positive);
  animation: highlight-pulse 2s ease-in-out;
}

.utterance-card.focused {
  border-left: 4px solid var(--accent-positive);
}

@keyframes highlight-pulse {
  0%, 100% {
    box-shadow: 
      8px 8px 16px var(--shadow-dark),
      -8px -8px 16px var(--shadow-light),
      0 0 0 2px var(--accent-positive);
  }
  50% {
    box-shadow: 
      8px 8px 16px var(--shadow-dark),
      -8px -8px 16px var(--shadow-light),
      0 0 0 4px var(--accent-positive);
  }
}

.utterance-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 12px;
}

.utterance-meta {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  align-items: center;
  flex: 1;
}

.speaker-badge {
  font-size: 14px;
  font-weight: 600;
  color: var(--text-primary);
  padding: 4px 8px;
  background: var(--card-bg);
  border-radius: 6px;
  box-shadow: 
    inset 2px 2px 4px var(--shadow-dark),
    inset -2px -2px 4px var(--shadow-light);
}

.timestamp {
  font-size: 12px;
  color: var(--text-secondary);
  font-family: 'Courier New', monospace;
}

.utterance-tags {
  display: flex;
  gap: 6px;
  flex-wrap: wrap;
}

.claim-tag {
  font-size: 11px;
  padding: 3px 8px;
  border-radius: 6px;
  font-weight: 500;
  text-transform: uppercase;
}

.tag-主張 {
  background: linear-gradient(135deg, #e3f2fd 0%, #bbdefb 100%);
  color: #1976d2;
}

.tag-反論 {
  background: linear-gradient(135deg, #fce4ec 0%, #f8bbd0 100%);
  color: #c2185b;
}

.tag-根拠 {
  background: linear-gradient(135deg, #f3e5f5 0%, #e1bee7 100%);
  color: #7b1fa2;
}

.utterance-icons {
  display: flex;
  gap: 8px;
}

.utterance-icon {
  width: 24px;
  height: 24px;
  color: var(--text-secondary);
  display: flex;
  align-items: center;
  justify-content: center;
}

.utterance-text {
  font-size: 15px;
  line-height: 1.6;
  color: var(--text-primary);
  margin-bottom: 12px;
}

.utterance-actions {
  display: flex;
  gap: 8px;
  align-items: center;
}

.play-button {
  width: 40px;
  height: 40px;
  color: var(--accent-positive);
}

.claim-button {
  padding: 8px 12px;
  display: flex;
  align-items: center;
  gap: 6px;
  color: var(--text-primary);
  font-size: 13px;
}

.claim-button-text {
  font-size: 13px;
}

@media (max-width: 768px) {
  .utterance-header {
    flex-direction: column;
    gap: 8px;
  }
  
  .utterance-meta {
    width: 100%;
  }
}

