.claims-tree-panel {
  margin-bottom: 16px;
}

.claims-tree-title {
  font-size: 18px;
  font-weight: 500;
  color: var(--text-primary);
  margin-bottom: 16px;
}

.claims-tree-content {
  display: flex;
  flex-direction: column;
  gap: 12px;
  max-height: 600px;
  overflow-y: auto;
  padding-right: 8px;
}

.claim-node-wrapper {
  position: relative;
}

.claim-node {
  cursor: pointer;
  transition: var(--transition);
  position: relative;
}

.claim-node:hover {
  transform: translateX(4px);
}

.claim-node.selected {
  box-shadow: 
    6px 6px 12px var(--shadow-dark),
    -6px -6px 12px var(--shadow-light),
    0 0 0 2px var(--accent-positive);
}

.claim-node.highlighted {
  animation: claim-pulse 1s ease-in-out;
  box-shadow: 
    6px 6px 12px var(--shadow-dark),
    -6px -6px 12px var(--shadow-light),
    0 0 0 2px var(--accent-positive);
}

@keyframes claim-pulse {
  0%, 100% {
    box-shadow: 
      6px 6px 12px var(--shadow-dark),
      -6px -6px 12px var(--shadow-light),
      0 0 0 2px var(--accent-positive);
  }
  50% {
    box-shadow: 
      6px 6px 12px var(--shadow-dark),
      -6px -6px 12px var(--shadow-light),
      0 0 0 4px var(--accent-positive);
  }
}

.claim-node-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 8px;
}

.claim-node-title {
  font-size: 15px;
  font-weight: 600;
  color: var(--text-primary);
  flex: 1;
}

.claim-node-type {
  font-size: 11px;
  padding: 3px 8px;
  border-radius: 6px;
  font-weight: 500;
  text-transform: uppercase;
  background: var(--card-bg);
  box-shadow: 
    inset 1px 1px 2px var(--shadow-dark),
    inset -1px -1px 2px var(--shadow-light);
  color: var(--text-secondary);
}

.claim-node-meta {
  display: flex;
  gap: 12px;
  font-size: 12px;
  color: var(--text-secondary);
  margin-bottom: 12px;
}

.claim-speaker {
  font-weight: 600;
}

.claim-utterances {
  color: var(--text-secondary);
}

.claim-node-actions {
  display: flex;
  gap: 8px;
  align-items: center;
}

.expand-button {
  width: 32px;
  height: 32px;
  color: var(--text-primary);
  font-size: 18px;
  font-weight: 600;
}

.reference-button {
  width: 32px;
  height: 32px;
  color: var(--text-primary);
}

.claim-references {
  margin-top: 12px;
  margin-left: 24px;
  padding: 12px;
}

.references-section {
  margin-bottom: 12px;
}

.references-section:last-child {
  margin-bottom: 0;
}

.references-title {
  font-size: 13px;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 8px;
}

.reference-item {
  margin-bottom: 12px;
  padding: 8px;
  background: var(--card-bg);
  border-radius: 8px;
}

.reference-item:last-child {
  margin-bottom: 0;
}

.reference-name {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 13px;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 6px;
}

.reference-name svg {
  color: var(--accent-positive);
}

.reference-text,
.reference-summary {
  font-size: 12px;
  line-height: 1.6;
  color: var(--text-secondary);
  margin-bottom: 4px;
}

.reference-meta {
  font-size: 11px;
  color: var(--text-secondary);
  font-style: italic;
}

.claim-children {
  margin-top: 12px;
  padding-left: 24px;
  border-left: 2px solid var(--shadow-dark);
  margin-left: 24px;
}

.level-0 {
  margin-left: 0;
}

.level-1 {
  margin-left: 0;
}

.level-2 {
  margin-left: 0;
}

@media (max-width: 768px) {
  .claims-tree-content {
    max-height: 400px;
  }
  
  .claim-node {
    margin-left: 0 !important;
  }
}

