.new-dispute-page {
  min-height: 100vh;
  background-color: var(--bg-color);
  padding-bottom: 20px;
}

.new-dispute-header {
  position: sticky;
  top: 0;
  z-index: 100;
  margin-bottom: 20px;
  padding: 16px 20px;
}

.header-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-button {
  width: 44px;
  height: 44px;
  color: var(--text-primary);
}

.page-title {
  font-size: 20px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
  flex: 1;
  text-align: center;
}

.new-dispute-content {
  padding: 0 16px;
  max-width: 600px;
  margin: 0 auto;
}

/* ステップインジケーター */
.step-indicator {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 32px;
  padding: 0 8px;
}

.step-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  flex: 1;
  position: relative;
}

.step-number {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: var(--card-bg);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  font-weight: 600;
  color: var(--text-secondary);
  box-shadow: 
    4px 4px 8px var(--shadow-dark),
    -4px -4px 8px var(--shadow-light);
  transition: var(--transition);
}

.step-item.active .step-number {
  background: var(--accent-positive);
  color: white;
  box-shadow: 
    4px 4px 8px var(--shadow-dark),
    -4px -4px 8px var(--shadow-light),
    0 0 0 2px var(--accent-positive);
}

.step-label {
  font-size: 11px;
  color: var(--text-secondary);
  text-align: center;
  white-space: nowrap;
}

.step-item.active .step-label {
  color: var(--text-primary);
  font-weight: 600;
}

.step-line {
  flex: 1;
  height: 2px;
  background: var(--shadow-dark);
  margin: 0 8px;
  transition: var(--transition);
}

.step-line.active {
  background: var(--accent-positive);
}

/* ステップコンテンツ */
.step-content {
  padding: 24px;
  margin-bottom: 20px;
}

.step-title {
  font-size: 22px;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 8px;
}

.step-description {
  font-size: 14px;
  color: var(--text-secondary);
  line-height: 1.6;
  margin-bottom: 24px;
}

.form-group {
  margin-bottom: 24px;
}

.form-label {
  display: block;
  font-size: 14px;
  font-weight: 500;
  color: var(--text-primary);
  margin-bottom: 8px;
}

.form-input {
  width: 100%;
  padding: 14px 16px;
  font-size: 16px;
  font-family: 'Noto Sans JP', sans-serif;
  color: var(--text-primary);
  background: var(--card-bg);
  border: none;
  border-radius: 12px;
  box-shadow: 
    inset 4px 4px 8px var(--shadow-dark),
    inset -4px -4px 8px var(--shadow-light);
  transition: var(--transition);
  -webkit-appearance: none;
}

.form-input:focus {
  outline: none;
  box-shadow: 
    inset 4px 4px 8px var(--shadow-dark),
    inset -4px -4px 8px var(--shadow-light),
    0 0 0 3px rgba(45, 52, 54, 0.1);
}

.char-count {
  font-size: 12px;
  color: var(--text-secondary);
  text-align: right;
  margin-top: 4px;
}

/* 録音セクション */
.recording-section {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.recording-card,
.upload-card {
  padding: 24px;
  text-align: center;
}

.recording-header,
.upload-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  margin-bottom: 20px;
}

.recording-title,
.upload-title {
  font-size: 18px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
}

.record-button,
.upload-button,
.stop-button,
.retry-button {
  width: 100%;
  padding: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  font-size: 16px;
  font-weight: 600;
  color: var(--text-primary);
}

.record-button {
  background: linear-gradient(135deg, #e8f5e9 0%, #c8e6c9 100%);
  color: var(--accent-positive);
}

.stop-button {
  background: linear-gradient(135deg, #ffebee 0%, #ffcdd2 100%);
  color: var(--accent-highlight);
}

.recording-active,
.recording-complete {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
}

.recording-indicator {
  display: flex;
  align-items: center;
  gap: 12px;
}

.recording-dot {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: var(--accent-highlight);
  animation: pulse 1.5s ease-in-out infinite;
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.5;
    transform: scale(1.2);
  }
}

.recording-text {
  font-size: 16px;
  font-weight: 600;
  color: var(--text-primary);
}

.recording-time {
  font-size: 20px;
  font-weight: 700;
  color: var(--accent-highlight);
  font-family: 'Courier New', monospace;
}

.complete-info {
  padding: 12px;
  background: var(--card-bg);
  border-radius: 12px;
  box-shadow: 
    inset 2px 2px 4px var(--shadow-dark),
    inset -2px -2px 4px var(--shadow-light);
}

.complete-text {
  font-size: 14px;
  color: var(--text-primary);
  font-weight: 600;
}

.upload-section {
  position: relative;
}

.divider {
  text-align: center;
  margin: 24px 0;
  position: relative;
}

.divider::before,
.divider::after {
  content: '';
  position: absolute;
  top: 50%;
  width: 40%;
  height: 1px;
  background: var(--shadow-dark);
}

.divider::before {
  left: 0;
}

.divider::after {
  right: 0;
}

.divider span {
  background: var(--bg-color);
  padding: 0 12px;
  color: var(--text-secondary);
  font-size: 13px;
  position: relative;
  z-index: 1;
}

.file-input {
  display: none;
}

.uploaded-file {
  margin-top: 16px;
  padding: 12px 16px;
  background: var(--card-bg);
  border-radius: 12px;
  box-shadow: 
    inset 2px 2px 4px var(--shadow-dark),
    inset -2px -2px 4px var(--shadow-light);
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
}

.file-name {
  font-size: 14px;
  color: var(--text-primary);
  flex: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.remove-button {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  border: none;
  background: var(--shadow-dark);
  color: var(--text-primary);
  font-size: 20px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: var(--transition);
  flex-shrink: 0;
}

.remove-button:hover {
  background: var(--accent-highlight);
  color: white;
}

/* 参加者セクション */
.participants-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 20px;
}

.participant-item {
  padding: 12px 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
}

.participant-name {
  font-size: 14px;
  color: var(--text-primary);
  flex: 1;
}

.remove-participant-button {
  width: 28px;
  height: 28px;
  border-radius: 50%;
  border: none;
  background: var(--shadow-dark);
  color: var(--text-primary);
  font-size: 18px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: var(--transition);
  flex-shrink: 0;
}

.remove-participant-button:hover {
  background: var(--accent-highlight);
  color: white;
}

.add-participant-button {
  width: 100%;
  padding: 14px;
  font-size: 16px;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 20px;
}

/* 確認セクション */
.confirmation-section {
  margin-bottom: 24px;
}

.confirmation-item {
  padding: 16px;
  background: var(--card-bg);
  border-radius: 12px;
  box-shadow: 
    inset 2px 2px 4px var(--shadow-dark),
    inset -2px -2px 4px var(--shadow-light);
  margin-bottom: 12px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.confirmation-label {
  font-size: 12px;
  color: var(--text-secondary);
  font-weight: 500;
}

.confirmation-value {
  font-size: 16px;
  color: var(--text-primary);
  font-weight: 600;
}

/* ステップアクション */
.step-actions {
  display: flex;
  gap: 12px;
  margin-top: 24px;
}

.back-button,
.next-button,
.start-button {
  flex: 1;
  padding: 14px 20px;
  font-size: 16px;
  font-weight: 600;
  color: var(--text-primary);
}

.start-button {
  background: linear-gradient(135deg, #e8f5e9 0%, #c8e6c9 100%);
  color: var(--accent-positive);
}

.next-button:disabled,
.start-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* iPhone16サイズ対応 */
@media (max-width: 430px) {
  .new-dispute-header {
    padding: 12px 16px;
  }

  .page-title {
    font-size: 18px;
  }

  .new-dispute-content {
    padding: 0 12px;
  }

  .step-content {
    padding: 20px 16px;
  }

  .step-number {
    width: 36px;
    height: 36px;
    font-size: 14px;
  }

  .step-label {
    font-size: 10px;
  }

  .step-title {
    font-size: 20px;
  }

  .step-description {
    font-size: 13px;
  }

  .form-input {
    padding: 12px 14px;
    font-size: 16px; /* iOSで拡大されないように */
  }

  .record-button,
  .upload-button,
  .stop-button,
  .retry-button {
    padding: 14px;
    font-size: 15px;
  }

  .step-actions {
    flex-direction: column;
  }

  .back-button,
  .next-button,
  .start-button {
    width: 100%;
  }
}

