* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --bg-color: #e9eef6;
  --card-bg: #f5f8fb;
  --shadow-light: #ffffff;
  --shadow-dark: #d1d9e6;
  --text-primary: #2c3e50;
  --text-secondary: #5a6c7d;
  --accent-positive: #4caf50;
  --accent-negative: #f44336;
  --accent-neutral: #9e9e9e;
  --accent-highlight: #D7263D;
  --border-radius: 18px;
  --transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

body {
  margin: 0;
  font-family: 'Noto Sans JP', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--bg-color);
  color: var(--text-primary);
  overflow-x: hidden;
  /* iPhone16対応 */
  max-width: 100vw;
  position: relative;
}

/* iPhone16サイズ対応（430px × 932px） */
@media (max-width: 430px) {
  body {
    font-size: 16px; /* iOSで拡大されないように */
  }
  
  /* タッチ操作の最適化 */
  button, a, [role="button"] {
    min-height: 44px; /* Apple推奨の最小タッチサイズ */
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0.1);
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* ニューモフィズムスタイル */
.neomorphic-card {
  background: var(--card-bg);
  border-radius: var(--border-radius);
  box-shadow: 
    8px 8px 16px var(--shadow-dark),
    -8px -8px 16px var(--shadow-light);
  padding: 20px;
  transition: var(--transition);
}

.neomorphic-card:active,
.neomorphic-card.pressed {
  box-shadow: 
    inset 4px 4px 8px var(--shadow-dark),
    inset -4px -4px 8px var(--shadow-light);
}

.neomorphic-button {
  background: var(--card-bg);
  border: none;
  border-radius: 12px;
  box-shadow: 
    4px 4px 8px var(--shadow-dark),
    -4px -4px 8px var(--shadow-light);
  cursor: pointer;
  transition: var(--transition);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 12px;
}

.neomorphic-button:hover {
  box-shadow: 
    6px 6px 12px var(--shadow-dark),
    -6px -6px 12px var(--shadow-light);
}

.neomorphic-button:active {
  box-shadow: 
    inset 2px 2px 4px var(--shadow-dark),
    inset -2px -2px 4px var(--shadow-light);
}

.neomorphic-inset {
  background: var(--card-bg);
  border-radius: var(--border-radius);
  box-shadow: 
    inset 4px 4px 8px var(--shadow-dark),
    inset -4px -4px 8px var(--shadow-light);
}

/* スクロールバー */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: var(--card-bg);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background: var(--shadow-dark);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #b8c4d3;
}

/* iPhone16サイズ対応 */
@media (max-width: 430px) {
  .neomorphic-card {
    padding: 16px;
    border-radius: 16px;
  }
}

