.login-page {
  min-height: 100vh;
  background-color: var(--bg-color);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  position: relative;
  overflow: hidden;
}

.login-page::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.1) 0%, transparent 70%);
  animation: pulse 20s ease-in-out infinite;
}

@keyframes pulse {
  0%, 100% {
    transform: scale(1);
    opacity: 0.5;
  }
  50% {
    transform: scale(1.1);
    opacity: 0.7;
  }
}

.login-container {
  width: 100%;
  max-width: 430px;
  position: relative;
  z-index: 1;
}

.login-header {
  text-align: center;
  margin-bottom: 32px;
}

.login-icon {
  width: 80px;
  height: 80px;
  margin: 0 auto 20px;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 
    8px 8px 16px var(--shadow-dark),
    -8px -8px 16px var(--shadow-light);
}

.login-icon img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  background: var(--card-bg);
  padding: 10px;
}

.login-title {
  font-size: 24px;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 8px;
}

.login-subtitle {
  font-size: 14px;
  color: var(--text-secondary);
  line-height: 1.6;
}

.login-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.login-error {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 12px 16px;
  background: #ffebee;
  border-radius: 12px;
  color: var(--accent-highlight);
  font-size: 14px;
}

.error-icon {
  font-size: 18px;
  flex-shrink: 0;
}

.error-text {
  flex: 1;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form-label {
  font-size: 14px;
  font-weight: 500;
  color: var(--text-primary);
}

.form-input {
  width: 100%;
  padding: 14px 16px;
  font-size: 16px;
  font-family: 'Noto Sans JP', sans-serif;
  color: var(--text-primary);
  background: var(--card-bg);
  border: none;
  border-radius: 12px;
  box-shadow: 
    inset 4px 4px 8px var(--shadow-dark),
    inset -4px -4px 8px var(--shadow-light);
  transition: var(--transition);
  -webkit-appearance: none;
}

.form-input:focus {
  outline: none;
  box-shadow: 
    inset 4px 4px 8px var(--shadow-dark),
    inset -4px -4px 8px var(--shadow-light),
    0 0 0 3px rgba(45, 52, 54, 0.1);
}

.form-input::placeholder {
  color: var(--text-secondary);
  opacity: 0.6;
}

.form-input:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.login-button {
  width: 100%;
  padding: 16px;
  font-size: 18px;
  font-weight: 600;
  color: var(--text-primary);
  margin-top: 8px;
  transition: var(--transition);
}

.login-button:hover:not(:disabled) {
  transform: translateY(-2px);
}

.login-button:active:not(:disabled) {
  transform: translateY(0);
}

.login-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.loading-spinner {
  display: inline-block;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.login-footer {
  text-align: center;
  margin-top: 8px;
}

.forgot-password {
  font-size: 14px;
  color: var(--text-secondary);
  text-decoration: none;
  transition: var(--transition);
}

.forgot-password:hover {
  color: var(--text-primary);
  text-decoration: underline;
}

.demo-info {
  margin-top: 24px;
  padding: 16px;
  text-align: center;
}

.demo-text {
  font-size: 12px;
  line-height: 1.6;
  color: var(--text-secondary);
  margin: 0;
}

.demo-text strong {
  color: var(--text-primary);
  font-weight: 600;
}

/* iPhone16サイズ対応 */
@media (max-width: 430px) {
  .login-page {
    padding: 16px;
  }

  .login-container {
    padding: 24px 20px;
  }

  .login-title {
    font-size: 22px;
  }

  .login-subtitle {
    font-size: 13px;
  }

  .form-input {
    padding: 12px 14px;
    font-size: 16px; /* iOSで拡大されないように */
  }

  .login-button {
    padding: 14px;
    font-size: 16px;
  }
}

/* 横画面対応 */
@media (max-width: 932px) and (orientation: landscape) {
  .login-page {
    padding: 20px;
  }

  .login-container {
    max-width: 500px;
  }
}

