.references-panel {
  margin-bottom: 16px;
}

.references-panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
  margin-bottom: 16px;
}

.references-panel-title {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 18px;
  font-weight: 500;
  color: var(--text-primary);
  margin: 0;
}

.references-panel-content {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.references-section {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.section-title {
  font-size: 16px;
  font-weight: 500;
  color: var(--text-primary);
  margin-bottom: 8px;
}

.references-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.reference-card {
  background: var(--card-bg);
  border-radius: 12px;
  box-shadow: 
    inset 2px 2px 4px var(--shadow-dark),
    inset -2px -2px 4px var(--shadow-light);
  overflow: hidden;
}

.reference-card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 12px 16px;
  background: none;
  border: none;
  cursor: pointer;
  transition: var(--transition);
}

.reference-card-header:hover {
  background: rgba(255, 255, 255, 0.5);
}

.reference-card-title {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  font-weight: 600;
  color: var(--text-primary);
  flex: 1;
  text-align: left;
}

.reference-card-title svg {
  color: var(--accent-positive);
  flex-shrink: 0;
}

.reference-card-content {
  padding: 12px 16px;
  border-top: 1px solid var(--shadow-dark);
}

.reference-text,
.reference-summary {
  font-size: 13px;
  line-height: 1.7;
  color: var(--text-secondary);
  margin-bottom: 12px;
}

.reference-meta {
  display: flex;
  gap: 12px;
  font-size: 11px;
  color: var(--text-secondary);
  font-style: italic;
  margin-bottom: 12px;
  flex-wrap: wrap;
}

.reference-full-text {
  font-size: 12px;
  line-height: 1.7;
  color: var(--text-secondary);
  padding: 12px;
  background: var(--bg-color);
  border-radius: 8px;
  box-shadow: 
    inset 2px 2px 4px var(--shadow-dark),
    inset -2px -2px 4px var(--shadow-light);
}

@media (max-width: 768px) {
  .reference-card-header {
    padding: 10px 12px;
  }
  
  .reference-card-content {
    padding: 10px 12px;
  }
}

