.speaker-list {
  margin-bottom: 16px;
}

.speaker-list-title {
  font-size: 18px;
  font-weight: 500;
  color: var(--text-primary);
  margin-bottom: 16px;
}

.speaker-cards {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.speaker-card {
  cursor: pointer;
  transition: var(--transition);
}

.speaker-card:hover {
  transform: translateY(-2px);
  box-shadow: 
    10px 10px 20px var(--shadow-dark),
    -10px -10px 20px var(--shadow-light);
}

.speaker-card.selected {
  box-shadow: 
    6px 6px 12px var(--shadow-dark),
    -6px -6px 12px var(--shadow-light),
    0 0 0 2px var(--accent-positive);
}

.speaker-card:focus {
  outline: none;
  box-shadow: 
    6px 6px 12px var(--shadow-dark),
    -6px -6px 12px var(--shadow-light),
    0 0 0 2px var(--accent-positive);
}

.speaker-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.speaker-id {
  font-size: 14px;
  font-weight: 600;
  color: var(--text-primary);
}

.speaker-icons {
  display: flex;
  gap: 8px;
}

.speaker-icon {
  width: 24px;
  height: 24px;
  color: var(--text-secondary);
  display: flex;
  align-items: center;
  justify-content: center;
}

.speaker-name {
  font-size: 16px;
  font-weight: 500;
  color: var(--text-primary);
  margin-bottom: 12px;
}

.speaker-fault {
  margin-bottom: 12px;
}

.fault-label {
  font-size: 12px;
  color: var(--text-secondary);
  margin-bottom: 8px;
}

.fault-circular {
  display: flex;
  justify-content: center;
}

.circular-gauge {
  position: relative;
  width: 60px;
  height: 60px;
}

.gauge-background {
  opacity: 0.3;
}

.gauge-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.gauge-percentage {
  font-size: 14px;
  font-weight: 700;
  color: var(--text-primary);
}

.speaker-stats {
  font-size: 12px;
  color: var(--text-secondary);
}

@media (max-width: 768px) {
  .speaker-cards {
    flex-direction: row;
    overflow-x: auto;
    padding-bottom: 8px;
  }
  
  .speaker-card {
    min-width: 200px;
  }
}

