.settlement-proposal-panel {
  margin-bottom: 16px;
}

.proposal-panel-title {
  font-size: 18px;
  font-weight: 500;
  color: var(--text-primary);
  margin-bottom: 16px;
}

.proposals-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.proposal-card {
  position: relative;
  transition: var(--transition);
}

.proposal-card.selected {
  box-shadow: 
    8px 8px 16px var(--shadow-dark),
    -8px -8px 16px var(--shadow-light),
    0 0 0 2px var(--accent-positive);
}

.proposal-card:hover {
  transform: translateY(-2px);
  box-shadow: 
    10px 10px 20px var(--shadow-dark),
    -10px -10px 20px var(--shadow-light);
}

.proposal-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 12px;
}

.proposal-title {
  font-size: 16px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
  flex: 1;
}

.selected-badge {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 4px 10px;
  background: linear-gradient(135deg, #e8f5e9 0%, #c8e6c9 100%);
  border-radius: 12px;
  font-size: 12px;
  font-weight: 600;
  color: var(--accent-positive);
}

.selected-badge svg {
  color: var(--accent-positive);
}

.proposal-description {
  font-size: 14px;
  color: var(--text-secondary);
  margin-bottom: 16px;
  line-height: 1.6;
}

.proposal-details {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 16px;
}

.proposal-amount {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px;
  background: var(--card-bg);
  border-radius: 12px;
  box-shadow: 
    inset 2px 2px 4px var(--shadow-dark),
    inset -2px -2px 4px var(--shadow-light);
}

.amount-label {
  font-size: 13px;
  color: var(--text-secondary);
}

.amount-value {
  font-size: 24px;
  font-weight: 700;
  color: var(--accent-positive);
}

.proposal-confidence {
  display: flex;
  align-items: center;
  gap: 12px;
}

.confidence-label {
  font-size: 13px;
  color: var(--text-secondary);
  min-width: 60px;
}

.confidence-gauge {
  flex: 1;
  height: 10px;
  background: var(--card-bg);
  border-radius: 5px;
  box-shadow: 
    inset 2px 2px 4px var(--shadow-dark),
    inset -2px -2px 4px var(--shadow-light);
  overflow: hidden;
}

.confidence-fill {
  height: 100%;
  background: linear-gradient(90deg, var(--accent-positive) 0%, #4caf50 100%);
  border-radius: 5px;
  transition: width 0.3s ease;
}

.confidence-value {
  font-size: 14px;
  font-weight: 600;
  color: var(--text-primary);
  min-width: 40px;
  text-align: right;
}

.proposal-reasoning {
  padding: 12px;
  background: var(--card-bg);
  border-radius: 12px;
  box-shadow: 
    inset 2px 2px 4px var(--shadow-dark),
    inset -2px -2px 4px var(--shadow-light);
  margin-bottom: 16px;
}

.proposal-reasoning p {
  font-size: 13px;
  line-height: 1.7;
  color: var(--text-secondary);
  margin: 0;
}

.proposal-fault-ratios {
  margin-bottom: 16px;
}

.fault-ratios-title {
  font-size: 13px;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 8px;
}

.fault-ratios-bars {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.fault-ratio-item {
  display: flex;
  align-items: center;
  gap: 8px;
}

.fault-ratio-label {
  font-size: 12px;
  color: var(--text-secondary);
  min-width: 80px;
}

.fault-ratio-bar-wrapper {
  flex: 1;
  height: 6px;
  background: var(--card-bg);
  border-radius: 3px;
  box-shadow: 
    inset 1px 1px 2px var(--shadow-dark),
    inset -1px -1px 2px var(--shadow-light);
  overflow: hidden;
}

.fault-ratio-bar {
  height: 100%;
  background: linear-gradient(90deg, #4caf50 0%, #ff9800 50%, #f44336 100%);
  border-radius: 3px;
  transition: width 0.3s ease;
}

.fault-ratio-value {
  font-size: 12px;
  font-weight: 600;
  color: var(--text-primary);
  min-width: 35px;
  text-align: right;
}

.proposal-actions {
  display: flex;
  gap: 8px;
}

.proposal-action-button {
  flex: 1;
  padding: 10px 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  color: var(--text-primary);
  font-size: 14px;
  font-weight: 500;
}

.proposal-action-button.selected {
  background: linear-gradient(135deg, #e8f5e9 0%, #c8e6c9 100%);
  color: var(--accent-positive);
}

.proposal-action-button.selected svg {
  color: var(--accent-positive);
}

.edit-button {
  flex: 0 0 auto;
  min-width: 80px;
}

@media (max-width: 768px) {
  .proposal-actions {
    flex-direction: column;
  }
  
  .edit-button {
    flex: 1;
  }
  
  .proposal-confidence {
    flex-wrap: wrap;
  }
  
  .confidence-label {
    width: 100%;
  }
}

