.home-page {
  min-height: 100vh;
  background-color: var(--bg-color);
  padding-bottom: 20px;
}

.home-header {
  position: sticky;
  top: 0;
  z-index: 100;
  margin-bottom: 20px;
  padding: 16px 20px;
}

.header-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.user-info {
  display: flex;
  align-items: center;
  gap: 12px;
}

.user-avatar {
  width: 48px;
  height: 48px;
  border-radius: 12px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 20px;
  font-weight: 600;
  box-shadow: 
    4px 4px 8px var(--shadow-dark),
    -4px -4px 8px var(--shadow-light);
}

.user-details {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.user-name {
  font-size: 16px;
  font-weight: 600;
  color: var(--text-primary);
}

.user-email {
  font-size: 12px;
  color: var(--text-secondary);
}

.logout-button {
  width: 44px;
  height: 44px;
  color: var(--text-primary);
}

.page-title {
  font-size: 24px;
  font-weight: 700;
  color: var(--text-primary);
  margin: 0;
}

.home-content {
  padding: 0 16px;
  max-width: 800px;
  margin: 0 auto;
}

.new-dispute-button {
  width: 100%;
  padding: 16px 24px;
  margin-bottom: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  font-size: 18px;
  font-weight: 600;
  color: var(--text-primary);
  transition: var(--transition);
}

.new-dispute-button:hover {
  transform: translateY(-2px);
}

.new-dispute-button:active {
  transform: translateY(0);
}

.button-icon {
  font-size: 28px;
  font-weight: 300;
  line-height: 1;
}

.button-text {
  font-size: 18px;
  font-weight: 600;
}

.cases-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.case-card {
  cursor: pointer;
  transition: var(--transition);
  padding: 20px;
}

.case-card:hover {
  transform: translateY(-2px);
  box-shadow: 
    10px 10px 20px var(--shadow-dark),
    -10px -10px 20px var(--shadow-light);
}

.case-card.selected {
  box-shadow: 
    8px 8px 16px var(--shadow-dark),
    -8px -8px 16px var(--shadow-light),
    0 0 0 2px var(--accent-positive);
}

.case-card:focus {
  outline: none;
  box-shadow: 
    8px 8px 16px var(--shadow-dark),
    -8px -8px 16px var(--shadow-light),
    0 0 0 2px var(--accent-positive);
}

.case-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 12px;
}

.case-title-wrapper {
  display: flex;
  align-items: center;
  gap: 8px;
  flex: 1;
}

.case-icon {
  font-size: 24px;
  flex-shrink: 0;
}

.case-title {
  font-size: 18px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
  flex: 1;
  line-height: 1.4;
}

.case-status {
  font-size: 12px;
  font-weight: 600;
  padding: 4px 10px;
  border-radius: 8px;
  background: var(--card-bg);
  box-shadow: 
    inset 2px 2px 4px var(--shadow-dark),
    inset -2px -2px 4px var(--shadow-light);
  flex-shrink: 0;
  margin-left: 12px;
}

.case-meta {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 13px;
  color: var(--text-secondary);
  margin-bottom: 12px;
}

.case-separator {
  color: var(--text-secondary);
  opacity: 0.5;
}

.case-summary {
  font-size: 14px;
  line-height: 1.6;
  color: var(--text-secondary);
  margin-bottom: 16px;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.case-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 16px;
}

.conflict-score-display {
  display: flex;
  align-items: center;
  gap: 8px;
  flex: 1;
}

.conflict-label {
  font-size: 12px;
  color: var(--text-secondary);
  min-width: 50px;
}

.conflict-bar-wrapper {
  flex: 1;
  height: 8px;
  background: var(--card-bg);
  border-radius: 4px;
  box-shadow: 
    inset 2px 2px 4px var(--shadow-dark),
    inset -2px -2px 4px var(--shadow-light);
  overflow: hidden;
}

.conflict-bar {
  height: 100%;
  border-radius: 4px;
  transition: width 0.3s ease;
}

.conflict-value {
  font-size: 12px;
  font-weight: 600;
  color: var(--text-primary);
  min-width: 35px;
  text-align: right;
}

.view-button {
  padding: 10px 16px;
  font-size: 14px;
  font-weight: 500;
  color: var(--text-primary);
  white-space: nowrap;
}

.empty-state {
  padding: 60px 20px;
  text-align: center;
}

.empty-text {
  font-size: 18px;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 8px;
}

.empty-subtext {
  font-size: 14px;
  color: var(--text-secondary);
}

/* iPhone16サイズ対応 */
@media (max-width: 430px) {
  .home-header {
    padding: 12px 16px;
  }

  .page-title {
    font-size: 20px;
  }

  .home-content {
    padding: 0 12px;
  }

  .case-card {
    padding: 16px;
  }

  .case-title {
    font-size: 16px;
  }

  .case-footer {
    flex-direction: column;
    align-items: stretch;
    gap: 12px;
  }

  .view-button {
    width: 100%;
  }
}

