.export-button {
  padding: 10px 16px;
  display: flex;
  align-items: center;
  gap: 8px;
  color: var(--text-primary);
  font-size: 14px;
  font-weight: 500;
}

.export-button-text {
  font-size: 14px;
}

@media (max-width: 768px) {
  .export-button-text {
    display: none;
  }
  
  .export-button {
    padding: 12px;
    width: 44px;
    height: 44px;
  }
}

