.daily-life-solutions-panel {
  margin-bottom: 16px;
}

.solutions-title {
  font-size: 18px;
  font-weight: 500;
  color: var(--text-primary);
  margin-bottom: 8px;
}

.solutions-description {
  font-size: 14px;
  color: var(--text-secondary);
  margin-bottom: 16px;
  line-height: 1.6;
}

.solutions-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.solution-card {
  padding: 20px;
  transition: var(--transition);
}

.solution-card:hover {
  transform: translateY(-2px);
  box-shadow: 
    10px 10px 20px var(--shadow-dark),
    -10px -10px 20px var(--shadow-light);
}

.solution-header {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  margin-bottom: 12px;
}

.solution-icon {
  font-size: 32px;
  flex-shrink: 0;
}

.solution-header-info {
  flex: 1;
}

.solution-name {
  font-size: 16px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0 0 6px 0;
}

.solution-type-badge {
  display: inline-block;
  padding: 3px 8px;
  font-size: 11px;
  font-weight: 500;
  color: var(--accent-positive);
  background: linear-gradient(135deg, #e8f5e9 0%, #c8e6c9 100%);
  border-radius: 6px;
}

.solution-description {
  font-size: 14px;
  line-height: 1.7;
  color: var(--text-secondary);
  margin-bottom: 16px;
}

.solution-meta {
  display: flex;
  gap: 20px;
  margin-bottom: 16px;
  padding: 12px;
  background: var(--card-bg);
  border-radius: 12px;
  box-shadow: 
    inset 2px 2px 4px var(--shadow-dark),
    inset -2px -2px 4px var(--shadow-light);
}

.meta-item {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.meta-label {
  font-size: 11px;
  color: var(--text-secondary);
  text-transform: uppercase;
}

.meta-value {
  font-size: 16px;
  font-weight: 700;
}

.meta-value.priority,
.meta-value.difficulty {
  font-size: 18px;
}

.solution-steps {
  margin-bottom: 16px;
  padding: 16px;
  background: var(--card-bg);
  border-radius: 12px;
  box-shadow: 
    inset 2px 2px 4px var(--shadow-dark),
    inset -2px -2px 4px var(--shadow-light);
}

.steps-title {
  font-size: 14px;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 12px;
}

.steps-list {
  margin: 0;
  padding-left: 20px;
}

.step-item {
  font-size: 13px;
  line-height: 1.8;
  color: var(--text-secondary);
  margin-bottom: 8px;
}

.step-item:last-child {
  margin-bottom: 0;
}

.solution-outcome {
  margin-bottom: 16px;
  padding: 12px;
  background: linear-gradient(135deg, #e3f2fd 0%, #bbdefb 100%);
  border-radius: 12px;
}

.outcome-label {
  display: block;
  font-size: 12px;
  font-weight: 600;
  color: #1976d2;
  margin-bottom: 6px;
  text-transform: uppercase;
}

.outcome-text {
  font-size: 14px;
  line-height: 1.7;
  color: var(--text-primary);
  margin: 0;
}

.apply-button {
  width: 100%;
  padding: 12px 16px;
  font-size: 14px;
  font-weight: 600;
  color: var(--accent-positive);
  background: linear-gradient(135deg, #e8f5e9 0%, #c8e6c9 100%);
}

.apply-button:hover {
  transform: translateY(-2px);
  box-shadow: 
    6px 6px 12px var(--shadow-dark),
    -6px -6px 12px var(--shadow-light);
}

.empty-solutions {
  padding: 40px 20px;
  text-align: center;
}

.empty-text {
  font-size: 14px;
  color: var(--text-secondary);
  margin: 0;
}

/* iPhone16サイズ対応 */
@media (max-width: 430px) {
  .solution-card {
    padding: 16px;
  }

  .solution-icon {
    font-size: 28px;
  }

  .solution-name {
    font-size: 15px;
  }

  .solution-meta {
    flex-direction: column;
    gap: 12px;
  }
}

