.category-selection {
  margin-bottom: 24px;
}

.category-title {
  font-size: 20px;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 8px;
}

.category-description {
  font-size: 14px;
  color: var(--text-secondary);
  margin-bottom: 20px;
  line-height: 1.6;
}

.category-options {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-bottom: 24px;
}

.category-card {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 20px;
  cursor: pointer;
  transition: var(--transition);
  position: relative;
}

.category-card:hover {
  transform: translateY(-2px);
  box-shadow: 
    10px 10px 20px var(--shadow-dark),
    -10px -10px 20px var(--shadow-light);
}

.category-card.selected {
  box-shadow: 
    8px 8px 16px var(--shadow-dark),
    -8px -8px 16px var(--shadow-light),
    0 0 0 2px var(--accent-positive);
}

.category-card:focus {
  outline: none;
  box-shadow: 
    8px 8px 16px var(--shadow-dark),
    -8px -8px 16px var(--shadow-light),
    0 0 0 2px var(--accent-positive);
}

.category-icon {
  font-size: 48px;
  flex-shrink: 0;
}

.category-info {
  flex: 1;
}

.category-name {
  font-size: 18px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0 0 4px 0;
}

.category-desc {
  font-size: 13px;
  color: var(--text-secondary);
  line-height: 1.5;
  margin: 0;
}

.check-mark {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: var(--accent-positive);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  font-weight: 600;
  flex-shrink: 0;
}

.subcategory-selection {
  margin-top: 24px;
  padding-top: 24px;
  border-top: 2px solid var(--shadow-dark);
}

.subcategory-title {
  font-size: 16px;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 12px;
}

.subcategory-options {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
}

.subcategory-button {
  padding: 10px 16px;
  font-size: 14px;
  font-weight: 500;
  color: var(--text-primary);
  border-radius: 12px;
}

.subcategory-button.selected {
  background: linear-gradient(135deg, #e8f5e9 0%, #c8e6c9 100%);
  color: var(--accent-positive);
}

/* iPhone16サイズ対応 */
@media (max-width: 430px) {
  .category-card {
    padding: 16px;
  }

  .category-icon {
    font-size: 40px;
  }

  .category-name {
    font-size: 16px;
  }

  .category-desc {
    font-size: 12px;
  }

  .subcategory-options {
    flex-direction: column;
  }

  .subcategory-button {
    width: 100%;
  }
}

