.analysis-detail-page {
  min-height: 100vh;
  background-color: var(--bg-color);
  display: flex;
  flex-direction: column;
}

.analysis-content {
  flex: 1;
  display: grid;
  grid-template-columns: 280px 1fr 320px;
  gap: 16px;
  padding: 16px;
  max-width: 1600px;
  margin: 0 auto;
  width: 100%;
  min-height: 0;
}

.analysis-left-column {
  display: flex;
  flex-direction: column;
  gap: 16px;
  overflow-y: auto;
  max-height: calc(100vh - 200px);
}

.analysis-center-column {
  display: flex;
  flex-direction: column;
  min-height: 0;
  overflow-y: auto;
  max-height: calc(100vh - 200px);
}

.analysis-right-column {
  display: flex;
  flex-direction: column;
  gap: 16px;
  overflow-y: auto;
  max-height: calc(100vh - 200px);
}

.analysis-bottom {
  padding: 16px;
  max-width: 1600px;
  margin: 0 auto;
  width: 100%;
}

.export-section {
  display: flex;
  justify-content: center;
  margin-top: 16px;
  padding: 16px;
}

/* iPhone16サイズ対応（390px × 844px） */
@media (max-width: 1200px) {
  .analysis-content {
    grid-template-columns: 240px 1fr 280px;
    gap: 12px;
    padding: 12px;
  }
}

@media (max-width: 992px) {
  .analysis-content {
    grid-template-columns: 1fr;
    grid-template-rows: auto auto auto;
  }
  
  .analysis-left-column,
  .analysis-center-column,
  .analysis-right-column {
    max-height: 500px;
  }
}

@media (max-width: 768px) {
  .analysis-content {
    padding: 12px;
    gap: 12px;
  }
  
  .analysis-left-column {
    max-height: 300px;
  }
  
  .analysis-center-column {
    max-height: 400px;
  }
  
  .analysis-right-column {
    max-height: 400px;
  }
}

@media (max-width: 430px) {
  /* iPhone16サイズ */
  .analysis-content {
    padding: 8px;
    gap: 8px;
  }
  
  .analysis-left-column,
  .analysis-center-column,
  .analysis-right-column {
    max-height: 350px;
  }
}

